:root {
  --background: #f8f9fa;
  --foreground: #2c3e50;
  --primary: #e74c3c;
  --secondary: #3498db;
  --accent: #f39c12;
  --success: #2ecc71;
  --card-bg: #ffffff;
  --border: #e1e8ed;
  --shadow: rgba(0, 0, 0, 0.1);
}
@media (prefers-color-scheme: dark) {
  :root {
    --background: #1a1a2e;
    --foreground: #eee;
    --card-bg: #16213e;
    --border: #2d3561;
    --shadow: rgba(0, 0, 0, 0.3);
  }
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background: var(--background);
  color: var(--foreground);
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  line-height: 1.6;
}
.home-container {
  min-height: 100vh;
  padding: 2rem 1rem;
}
.header {
  text-align: center;
  margin-bottom: 3rem;
  animation: fadeInDown 0.6s ease-out;
}
.title {
  font-size: 3.5rem;
  font-weight: 800;
  background: linear-gradient(135deg, var(--primary), var(--accent));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin-bottom: 0.5rem;
  letter-spacing: -1px;
}
.subtitle {
  font-size: 1.2rem;
  color: #7f8c8d;
  font-weight: 300;
}
.content-wrapper {
  max-width: 1400px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 400px 1fr;
  gap: 2rem;
  align-items: start;
}
@media (max-width: 1024px) {
  .content-wrapper {
    grid-template-columns: 1fr;
  }
}
.control-panel {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}
.card {
  background: var(--card-bg);
  border-radius: 16px;
  padding: 1.5rem;
  box-shadow: 0 4px 6px var(--shadow);
  border: 1px solid var(--border);
  animation: fadeInUp 0.6s ease-out;
}
.section-title {
  font-size: 1.3rem;
  font-weight: 700;
  margin-bottom: 1rem;
  color: var(--foreground);
}
.letter-selector {
  display: flex;
  gap: 0.75rem;
  justify-content: center;
  flex-wrap: wrap;
}
.letter-checkbox {
  position: relative;
  cursor: pointer;
  user-select: none;
  animation: fadeIn 0.4s ease-out both;
}
.checkbox-input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}
.letter-label {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 60px;
  height: 60px;
  font-size: 1.8rem;
  font-weight: 800;
  border: 3px solid var(--border);
  border-radius: 12px;
  background: var(--card-bg);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}
.letter-checkbox:hover .letter-label {
  transform: translateY(-2px);
  box-shadow: 0 6px 12px var(--shadow);
}
.letter-checkbox.active .letter-label {
  background: linear-gradient(135deg, var(--primary), var(--accent));
  color: white;
  border-color: var(--primary);
  transform: scale(1.05);
}
.info-text {
  margin-top: 1rem;
  font-size: 0.9rem;
  color: #7f8c8d;
  text-align: center;
  padding: 0.75rem;
  background: var(--background);
  border-radius: 8px;
}
.current-number-display {
  background: linear-gradient(135deg, var(--primary), var(--accent));
  border-radius: 16px;
  padding: 2rem;
  box-shadow: 0 8px 16px var(--shadow);
  animation: bounceIn 0.6s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}
.current-number {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  color: white;
}
.number-letter {
  font-size: 4rem;
  font-weight: 900;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
}
.number-value {
  font-size: 5rem;
  font-weight: 900;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
}
.button-group {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
}
.btn {
  flex: 1;
  min-width: 140px;
  padding: 1rem 1.5rem;
  font-size: 1.1rem;
  font-weight: 600;
  border: none;
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 6px var(--shadow);
  text-align: center;
}
.btn-primary {
  background: linear-gradient(135deg, var(--primary), #c0392b);
  color: white;
}
.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 12px var(--shadow);
}
.btn-primary:active {
  transform: translateY(0);
}
.btn-secondary {
  background: var(--card-bg);
  color: var(--foreground);
  border: 2px solid var(--border);
}
.btn-secondary:hover {
  background: var(--background);
  transform: translateY(-2px);
}
.stats {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
}
.stat-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}
.stat-label {
  font-size: 1rem;
  opacity: 0.9;
}
.stat-value {
  font-size: 1.5rem;
  font-weight: 700;
}
.recent-numbers {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-top: 1rem;
  padding-top: 1rem;
  border-top: 1px solid rgba(255, 255, 255, 0.2);
}
.recent-label {
  font-size: 0.9rem;
  opacity: 0.9;
  width: 100%;
  margin-bottom: 0.25rem;
}
.recent-number {
  padding: 0.4rem 0.8rem;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 8px;
  font-weight: 600;
  font-size: 0.9rem;
}
.board-container {
  animation: fadeInUp 0.6s ease-out 0.2s both;
}
.bingo-board {
  background: var(--card-bg);
  border-radius: 16px;
  padding: 2rem;
  box-shadow: 0 4px 6px var(--shadow);
  border: 1px solid var(--border);
}
.board-title {
  font-size: 1.8rem;
  font-weight: 700;
  text-align: center;
  margin-bottom: 1.5rem;
  color: var(--foreground);
}
.board-grid {
  display: flex;
  gap: 0.5rem;
  justify-content: center;
}
.board-column {
  display: flex;
  flex-direction: column;
  gap: 0;
}
.column-header {
  font-size: 1.8rem;
  font-weight: 900;
  color: white;
  padding: 0.75rem;
  text-align: center;
  border-radius: 8px 8px 0 0;
  box-shadow: 0 2px 4px var(--shadow);
  height: 70px;
  min-height: 70px;
  width: 70px;
  min-width: 74px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.column-numbers {
  display: flex;
  flex-direction: column;
  gap: 2px;
  background: var(--border);
  padding: 2px;
  border-radius: 0 0 8px 8px;
}
.number-cell {
  width: 70px;
  height: 70px;
  min-width: 70px;
  min-height: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--card-bg);
  font-weight: 600;
  font-size: 0.95rem;
  transition: all 0.2s ease;
  position: relative;
}
.number-cell.selected {
  background: linear-gradient(135deg, #f39c12, #e67e22);
  color: white;
  font-weight: 800;
  animation: pulse 0.4s ease;
}
.number-cell.current {
  animation: currentPulse 1s ease infinite;
  box-shadow: 0 0 20px var(--accent);
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes bounceIn {
  0% {
    opacity: 0;
    transform: scale(0.3);
  }
  50% {
    opacity: 1;
    transform: scale(1.05);
  }
  70% {
    transform: scale(0.9);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes pulse {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
}
@keyframes currentPulse {
  0%, 100% {
    box-shadow: 0 0 20px var(--accent);
  }
  50% {
    box-shadow: 0 0 40px var(--accent);
  }
}
@keyframes pulse {
  50% {
    opacity: 0.5;
  }
}
